.contact {
  display: flex;
  height: 75vh;
  margin: 0 60px;
  margin-top: 150px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.contact-title {
  display: flex;
}

.contact-box {
  border: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  width: 600px;
  height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05);
}

.contact-box h2 {
  margin-bottom: -30px;
}

.contact-box .secondary {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.contact-box .title {
  margin-top: 30px;
  margin-bottom: 30px;
  color: rgba(0, 0, 0, 0.356);
}

.contact-box #photographer {
  margin-top: -30px;
}

/* .contact-box .icons {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 50px;
} */

.contact-box hr {
  height: 100px;
  width: 1px;
  color: rgb(0, 0, 0);
  margin-left: 30px;
  margin-right: 30px;
}

.contact-box .icons {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: auto;
}

.icons img {
  width: 5vw;
  padding: 20px;
}

@media screen and (max-width: 550px) {
  .contact-box {
    width: 400px;
  }
}
